.page {
  overflow-x: hidden;
}

// .page__main-header {
//   // position: relative;
//   // margin-bottom: 27px;
// }

.page__body {
  .fontRegular(@fontRegular);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-width: 320px;
  overflow-x: hidden;
  // width: 320px;
  // margin: 0 auto;
}

.page__catalog-main-container {
  margin-bottom: 35px;

}

.page__main-header {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #ebebeb;
  // margin: 0 -20px 0 -20px;

}

@media (min-width: @tablet-width) {
  .page__main-header {
    padding-top: 25px;
  }

  .page__main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding: 0 30px 0;
    width: @tablet-width;
    border-bottom: none;
  }
}

@media (min-width: @desktop-width) {
    .page__main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 74px;
    padding: 0 110px 0;
    width: @desktop-width;
  }

  .page__main-footer {
    position: relative;
  }


}
